
import { Injectable } from '@angular/core';


@Injectable({
    providedIn: 'root'
})
/*

*/
export class Storage_LoginInfo {

    private userinfo: string = "logo_userinfo";
    SetInfo(data: any) {
        window.localStorage.setItem(this.userinfo, data);
    }
    GetUserInfo() {
        var data = window.localStorage.getItem(this.userinfo);
        return JSON.parse(data);
    }
    //token
    GetToken() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.access_token;
        return null;
    }
    //userid
    GetSystemUserId() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.systemuserid;
        return null;
    }
    //
    GetDomainname() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.domainname;
        return null;
    }
    //
    GetLastname() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.lastname;
        return null;
    }
    //
    GetFirstname() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.firstname;
        return null;
    }
    //
    GetStaffid() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.mcs_staffid;
        return null;
    }
    //
    GetDealerid() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.mcs_dealerid;
        return null;
    }
    //
    GetDealername() {
        var data = this.GetUserInfo();
        if (data != null)
            return data.mcs_dealername;
        return null;
    }
}


